header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999;
}
header .ship {
  background: #181818;
  color: #bdbdbd;
  font-size: 0.8em;
  font-weight: 500;
  letter-spacing: 1.5px;
  padding: 10px 0;
  text-align: center;
}
header .background {
  background: #fff;
  width: 100%;
}
header .background .whitebar {
  margin: 0 auto;
  max-width: 87%;
  padding: 15px 0;
}
header .background .whitebar .logo {
  display: inline-block;
  font-size: 1.5em;
  font-weight: 900;
  position: relative;
  right: 45%;
}
header .background .whitebar .logo a {
  color: black;
}
header .background .whitebar .logo a:hover {
  color: #a6a2a2;
  text-decoration: none;
}
header .background .whitebar .cart-menu {
  display: inline-block;
  float: right;
  padding: 5px 5px;
}
header .background .whitebar .cart-menu .cart {
  display: inline-block;
  margin: 0 15px 0 0;
  cursor: pointer;
}
header .background .whitebar .cart-menu .menu {
  display: inline-block;
  color: #000;
  cursor: pointer;
}
header .background .whitebar .list-cart {
  height: 100%;
  list-style: none;
  position: fixed;
  width: 0;
  z-index: 2;
  top: 0;
  right: 0;
  background: #111;
  opacity: 0.9;
  overflow: hidden;
  padding: 5px 0 0 0;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
header .background .whitebar .list-cart .list-cart-header {
  color: #ccc;
  display: block;
  font-size: 23px;
  padding: 15px 0;
  text-align: center;
}
header .background .whitebar .list-cart .list-cart-header .header {
  color: #ccc;
  display: block;
}
header .background .whitebar .list-cart .list-cart-header .btn-close {
  display: block;
  margin: 0 0 0 10px;
  font-size: 80px;
  text-align: right;
  cursor: pointer;
}
header .background .whitebar .list-cart .shoppingCart {
  background: #fff;
  color: black;
  padding: 20px 30px;
  height: 80%;
  width: 90%;
  overflow-y: auto;
}
header .background .whitebar .list-cart .shoppingCart #show-cart li {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  font-family: sans-serif;
  padding: 10px 0;
}
header .background .whitebar .list-cart .shoppingCart #show-cart li .leftBlock {
  display: block;
  grid-colum: 1/2;
  grid-row: 1/2;
}
header
  .background
  .whitebar
  .list-cart
  .shoppingCart
  #show-cart
  li
  .middleBlock {
  display: block;
  grid-column: 2/3;
  grid-row: 1/2;
}
header
  .background
  .whitebar
  .list-cart
  .shoppingCart
  #show-cart
  li
  .middleBlock
  h6 {
  font-size: 0.83em;
  padding: 2px 0 0 0;
}
header
  .background
  .whitebar
  .list-cart
  .shoppingCart
  #show-cart
  li
  .middleBlock
  .count-item {
  width: 45%;
}
header
  .background
  .whitebar
  .list-cart
  .shoppingCart
  #show-cart
  li
  .rightBlock {
  grid-column: 3/4;
  grid-row: 1/2;
  justify-self: center;
}
header
  .background
  .whitebar
  .list-cart
  .shoppingCart
  #show-cart
  li
  .rightBlock
  button {
  background: #fff;
  border: none;
  cursor: pointer;
  font-weight: 600;
}
header .background .whitebar .list-cart li a {
  color: #ccc;
}
header .background .whitebar .list-cart li a:hover {
  color: #a6a2a2;
}
header .background .whitebar .side-nav {
  height: 100%;
  list-style: none;
  position: fixed;
  width: 0;
  z-index: 1;
  top: 0;
  right: 0;
  background: #111;
  opacity: 0.9;
  overflow-x: hidden;
  padding-top: 60px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
header .background .whitebar .side-nav .btn-close {
  margin: 0 0 0 200px;
  font-size: 80px;
  text-align: none;
  cursor: pointer;
}
header .background .whitebar .side-nav li {
  padding: 10px 30px 10px 10px;
  font-size: 22px;
  color: #ccc;
  display: block;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  text-align: center;
}
header .background .whitebar .side-nav li a {
  color: #ccc;
}
header .background .whitebar .side-nav li a:hover {
  color: #a6a2a2;
}

section .info {
  background: #181818;
  color: #bdbdbd;
  padding: 60px 0 65px 0;
}
section .info .infoWrapper .infoOne {
  display: inline-block;
  /* font-family: "Roboto", sans-serif; */
  font-size: 14px;
  font-weight: 600;
  padding: 0 0 0 205px;
  text-transform: capitalize;
}
section .info .infoWrapper .infoOne p {
  padding: 10px 0;
}
section .info .infoWrapper .infoTwo {
  display: inline-block;
  /* font-family: "Roboto", sans-serif; */
  font-size: 14px;
  font-weight: 600;
  padding: 0 0 0 90px;
}
section .info .infoWrapper .infoTwo p {
  padding: 10px;
}
section .info .emailInput {
  display: inline-block;
  float: right;
  padding: 50px 230px 0 0;
}
section .info .emailInput input {
  background: #181818;
  border: none;
  border-bottom: 1px solid #fff;
  margin: 0 5px 0 0;
  padding: 0 80px 11px 0;
}
section .info .emailInput span {
  background: #fff;
  color: #181818;
  cursor: pointer;
  padding: 10px 30px;
  text-transform: uppercase;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}
section .info .emailInput span:hover {
  background: black;
  color: white;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}

footer {
  background: #2e2d2d;
  color: #8f9797;
  padding: 18px;
  text-align: center;
}
footer a {
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}
footer a:hover {
  text-decoration: underline;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}

@media only screen and (max-width: 600px) {
  section .info .infoWrapper {
    text-align: center;
  }
  section .info .infoWrapper .infoOne {
    padding: 0 0 0 26px;
  }
  section .info .infoWrapper .infoTwo {
    padding: 0 0 30px 59px;
  }
  section .info .emailInput {
    float: none;
    padding: 0;
  }
  section .info .emailInput input {
    padding: 0;
  }
}
@media only screen and (max-width: 320px) {
  section .info .emailInput {
    padding: 9px;
  }
}
@media only screen and (max-width: 375px) {
  section .info .emailInput {
    padding: 0 0 0 35px;
  }
}
@media only screen and (max-width: 425px) {
  section .info .emailInput {
    padding: 15px 0 0 76px;
  }
}

